

/* ----------MAIN NAV MENU ------------- */

.main-nav-outer {
    display: none;
    width: 100%;
}

.main-nav {    
    position: relative;
    list-style-type: none;    
    background-color: #333645; 
}

.ul-container {
    display: inline-flex;
    flex-direction: row;
    color: white;
    list-style-type: none;
    width: 100%;
    justify-content: center;
    font-size: 18px;
    li {
        display: flex;
        flex-direction: column;
        color: white;
        text-transform: uppercase;
            a {
            color: white;  
            text-decoration: none; 
            width: 100%;
        }                
    }
}

.men-but {
    flex-basis: 20%;
    display: flex;
    border-right: 1px solid hsla(0,0%,100%,.1);
    border-left: 1px solid rgba(0,0,0,.25); 
}


.fas-1 {
    color: #926fb1;
    border-radius: 50%;
}

.fas-2 {
    color: #4183d7;
    border-radius: 50%;
 }

 .fas-3 {
    color: #f62459;
    border-radius: 50%;
 }

 .fas-4 {
    color: #67809f;
    border-radius: 50%;
 }

 .fas-5 {
    color: #2ecc71;
    border-radius: 50%;
 }

 .fas-6 {
    color: #f62459;
    border-radius: 50%;
 }

 #nav-men-wd {
     display: flex;
     flex-direction: column;
 }

 #nav-men-wd::after {
    padding-bottom: 100px;
    background-color: transparent;
}

 #nav-men-is {
    display: flex;
    flex-direction: column;
}

#nav-men-ts {
    display: flex;
    flex-direction: column;
}

#nav-men-bs {
    display: flex;
    flex-direction: column;
}

#nav-men-dm {
    display: flex;
    flex-direction: column;
}

#nav-men-cs {
    display: flex;
    flex-direction: column;
}

 /* --------- Nav menu hover ---------*/

#services-list-wd,
#services-list-it,
#services-list-ts,
#services-list-bs,
#services-list-dm,
#services-list-cs
 {
    visibility: hidden;
    position: absolute;
    z-index: 1;
    display: inline-flex;
    top: 100%;    
    left: 0;
    width: 100vw;
}

.sl-container {
    display: flex;
    position: relative;
    height: 45px;
    width: 1200px;
    margin-left: auto;
    margin-right: auto;
    justify-content: space-around;
    font-size: 16px;
    padding-top: 10px;
}

.services-list-wd {
    background-color: #79539a;
}

.services-list-it {
    background-color: #286abd
}

.services-list-ts {
    background-color: #bc2c28;
}

.services-list-bs {
    background-color: #526781;
}

.services-list-dm {
    background-color: #25a25a;
}

.services-list-cs {
    background-color: #de093f;
}

#nav-men-wd:hover ~ #services-list-wd {
   visibility: visible;
}

#nav-men-is:hover ~ #services-list-it {
    visibility: visible;
 }

 #nav-men-ts:hover ~ #services-list-ts {
    visibility: visible;
 }

 #nav-men-bs:hover ~ #services-list-bs {
    visibility: visible;
 }

 #nav-men-dm:hover ~ #services-list-dm {
    visibility: visible;
 }

 #nav-men-cs:hover ~ #services-list-cs {
    visibility: visible;
 }

